/* eslint-disable react/destructuring-assignment */
/* eslint-disable react/prop-types */
/* eslint-disable dot-notation */
import React, { useState } from "react";
import DataTable from "examples/Tables/DataTable";
import IsLoad from "services/IsLoad";
import DeleteIcon from "@mui/icons-material/Delete";
import { useQuery, useMutation } from "react-query";
import { GetSymbol, DeleteSymbol } from "services/backend_api";
import MDBox from "components/MDBox";
import MDTypography from "components/MDTypography";
import MDButton from "components/MDButton";
import { Toast } from "utils/constant";
// Dialog box componants
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";

function SymbolTable(props) {
  const [currentRowId, setcurrentRowId] = useState(0);
  const [open, setOpen] = useState(false);
  const handleClose = () => {
    setOpen(false);
  };
  const handleClickOpen = (id) => {
    setcurrentRowId(id);
    setOpen(true);
  };
  const { data, refetch, isLoading } = useQuery("GetSymbol", () =>
    GetSymbol()
      .then((res) => {
        if (res) {
          if (res.error) {
            Toast.fire({
              icon: "error",
              title: `${res.error}`,
            });
          }
          return res;
        }
        refetch();
        return false;
      })
      .catch((e) => {
        Toast.fire({
          icon: "error",
          title: `${e}`,
        });
      })
  );

  const mutation = useMutation("DeleteSymbol", (values) =>
    DeleteSymbol(values)
      .then((res) => {
        // console.log(res);
        if (res) {
          if (res.error) {
            Toast.fire({
              icon: "error",
              title: `${res.error}`,
            });
          } else {
            Toast.fire({
              icon: "success",
              title: `Symbol Deleted Successfully`,
            });
          }
          refetch();
          return res;
        }
        return false;
      })
      .catch((e) => {
        Toast.fire({
          icon: "error",
          title: `${e}`,
        });
      })
  );
  const deletedata = (id) => {
    mutation.mutate(id);
    setOpen(false);
  };
  if (props.refetchData) {
    refetch();
    props.setRefetchData(false);
  }
  return (
    <>
      <MDBox
        mx={2}
        mt={-4}
        py={3}
        px={2}
        variant="gradient"
        bgColor="info"
        borderRadius="lg"
        coloredShadow="info"
      >
        <MDTypography variant="h6" color="white">
          Symbol List
        </MDTypography>
      </MDBox>
      <MDBox>
        {isLoading || data === undefined ? (
          <IsLoad />
        ) : (
          <DataTable
            table={{
              columns: [
                { Header: "id", accessor: "id", align: "center" },
                { Header: "Type", accessor: "type", align: "center" },
                { Header: "Name", accessor: "name", align: "center" },
                { Header: "action", accessor: "action", align: "center" },
              ],
              rows: data.map((value, ind) => {
                // eslint-disable-next-line prefer-const
                let x = {};
                x["id"] = 1 + ind;
                x["type"] = value["type"];
                x["name"] = value["name"];
                x["action"] = (
                  <>
                    <MDButton>
                      <DeleteIcon
                        variant="gradient"
                        color="error"
                        onClick={() => {
                          handleClickOpen(value["id"]);
                        }}
                        aria-label="delete"
                        sx={{
                          fontWeight: "bold",
                          cursor: "pointer",
                          transform: "scale(1.5)",
                        }}
                      >
                        Delete
                      </DeleteIcon>
                    </MDButton>
                    <Dialog
                      open={open}
                      onClose={handleClose}
                      aria-labelledby="alert-dialog-title"
                      aria-describedby="alert-dialog-description"
                    >
                      <DialogTitle id="alert-dialog-title">Information</DialogTitle>
                      <DialogContent>
                        <DialogContentText id="alert-dialog-description">
                          Are you sure you want to delete
                        </DialogContentText>
                      </DialogContent>
                      <DialogActions>
                        <MDButton onClick={handleClose}>No</MDButton>
                        <MDButton
                          onClick={() => {
                            deletedata(currentRowId);
                          }}
                        >
                          Yes
                        </MDButton>
                      </DialogActions>
                    </Dialog>
                  </>
                );
                return x;
              }),
            }}
          />
        )}
      </MDBox>
    </>
  );
}

export default SymbolTable;
