import { Card, Grid, Select } from "@mui/material";
import MDBox from "components/MDBox";
import MDInput from "components/MDInput";
import MDTypography from "components/MDTypography";
import React, { useState } from "react";
import { useMutation } from "react-query";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import { Formik } from "formik";
import MDButton from "components/MDButton";
import ArrowDropDownIcon from "@mui/icons-material/ArrowDropDown";
import MenuItem from "@mui/material/MenuItem";
import { CreateAlgo } from "services/backend_api";
import { Toast } from "utils/constant";
import AlgoTable from "./AlgoTable";

// const options = [
//   { value: "Positional", label: "Positional" },
//   { value: "Intraday", label: "Intraday" },
// ];
function AddAlgoUI() {
  const [refetchData, setRefetchData] = useState(false);
  const mutation = useMutation("CreateAlgo", (values) =>
    CreateAlgo(values.name, values.algoType)
      .then((res) => {
        // console.log(res);
        if (res) {
          if (res.error) {
            Toast.fire({
              icon: "error",
              title: `${res.error}`,
            });
          } else {
            Toast.fire({
              icon: "success",
              title: `Algo Created Successfully`,
            });
          }
          setRefetchData(true);
          return res;
        }
        return false;
      })
      .catch((e) => {
        Toast.fire({
          icon: "error",
          title: `${e}`,
        });
      })
  );
  return (
    <>
      <MDBox mt={2}>
        <Grid container>
          <Grid item xs={12} sm={12} md={4} lg={4} p={1} mb={3}>
            <Card style={{ padding: "15px" }}>
              <MDBox
                mx={2}
                mt={-4}
                py={3}
                px={2}
                variant="gradient"
                bgColor="info"
                borderRadius="lg"
                coloredShadow="info"
              >
                <MDTypography variant="h6" color="white">
                  Add Algo
                </MDTypography>
              </MDBox>
              <Formik
                initialValues={{
                  algoType: "",
                  name: "",
                }}
                validate={(values) => {
                  const errors = {};
                  if (!values.algoType) {
                    errors.algoType = (
                      <div style={{ color: "red", fontSize: "12px" }}>Required</div>
                    );
                  }
                  if (!values.name) {
                    errors.name = <div style={{ color: "red", fontSize: "12px" }}>Required</div>;
                  }
                  return errors;
                }}
                onSubmit={(values, { resetForm }) => {
                  // console.log(values, symType);
                  mutation.mutate(values);
                  resetForm({ values: "" });
                }}
              >
                {({
                  values,
                  errors,
                  touched,
                  handleChange,
                  handleBlur,
                  handleSubmit,
                  /* and other goodies */
                }) => (
                  <MDBox sx={{ width: "100%" }} pl={2} pr={2}>
                    <MDBox mb={2} mt={4}>
                      <FormControl style={{ width: "100%" }}>
                        <InputLabel id="demo-simple-select-label">Algo Type</InputLabel>
                        <Select
                          sx={{ height: 45 }}
                          IconComponent={() => <ArrowDropDownIcon />}
                          labelId="demo-simple-select-label"
                          label="Algo Type"
                          id="algoType"
                          name="algoType"
                          onChange={(e) => {
                            // eslint-disable-next-line no-param-reassign
                            values.algoType = e.target.value;
                          }}
                        >
                          <MenuItem value="Positional">Positional</MenuItem>
                          <MenuItem value="Intraday">Intraday</MenuItem>
                        </Select>
                      </FormControl>
                      {errors.algoType && touched.algoType && errors.algoType}
                    </MDBox>
                    <MDBox mb={2} mt={3}>
                      <MDInput
                        type="text"
                        label="Name"
                        name="name"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.name}
                        fullWidth
                      />
                      {errors.name && touched.name && errors.name}
                    </MDBox>
                    <MDBox mb={2} mt={3} justifyContent="center" textAlign="center">
                      <MDButton
                        variant="gradient"
                        type="submit"
                        color="info"
                        onClick={() => {
                          handleSubmit();
                        }}
                      >
                        Add Algo
                      </MDButton>
                    </MDBox>
                  </MDBox>
                )}
              </Formik>
            </Card>
          </Grid>
          <Grid item xs={12} sm={12} md={8} lg={8} p={1}>
            <Card style={{ padding: "15px" }}>
              <AlgoTable refetchData={refetchData} setRefetchData={setRefetchData} />
            </Card>
          </Grid>
        </Grid>
      </MDBox>
    </>
  );
}

export default AddAlgoUI;
