/* eslint-disable dot-notation */
import React from "react";
import DataTable from "examples/Tables/DataTable";
import IsLoad from "services/IsLoad";
import { useQuery } from "react-query";
import { GetLog } from "services/backend_api";
import MDBox from "components/MDBox";
import MDTypography from "components/MDTypography";
// import { Toast } from "utils/constant";
import { Card } from "@mui/material";
import { Toast } from "utils/constant";

function LogTable() {
  const { data, isLoading } = useQuery("GetLogs", () =>
    GetLog()
      .then((res) => {
        if (res) {
          if (res.error) {
            Toast.fire({
              icon: "error",
              title: `${res.error}`,
            });
          }
          return res;
        }
        return false;
      })
      .catch((e) => {
        Toast.fire({
          icon: "error",
          title: `${e}`,
        });
      })
  );

  return (
    <>
      <MDBox mt={5}>
        <Card style={{ padding: "15px" }}>
          <MDBox
            mx={2}
            mt={-6}
            py={3}
            px={2}
            variant="gradient"
            bgColor="info"
            borderRadius="lg"
            coloredShadow="info"
          >
            <MDTypography variant="h6" color="white">
              Logs
            </MDTypography>
          </MDBox>
          <MDBox>
            {isLoading || data === undefined ? (
              <IsLoad />
            ) : (
              <DataTable
                table={{
                  columns: [
                    { Header: "id", accessor: "id", align: "center" },
                    { Header: "User", accessor: "user", align: "center" },
                    { Header: "Action", accessor: "action", align: "center" },
                    { Header: "Description", accessor: "description", align: "center" },
                    { Header: "Date", accessor: "date_and_time", align: "center" },
                  ],
                  rows: data.map((value, ind) => {
                    // eslint-disable-next-line prefer-const
                    let x = {};
                    x["id"] = 1 + ind;
                    x["user"] = value["user"];
                    x["action"] = value["action"];
                    x["description"] = value["description"];
                    x["date_and_time"] = value["time"];
                    return x;
                  }),
                }}
              />
            )}
          </MDBox>
        </Card>
      </MDBox>
    </>
  );
}

export default LogTable;
