/* eslint-disable dot-notation */
import { Card, Grid, Select } from "@mui/material";
import MDBox from "components/MDBox";
import MDInput from "components/MDInput";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import ArrowDropDownIcon from "@mui/icons-material/ArrowDropDown";
import MenuItem from "@mui/material/MenuItem";
import MDTypography from "components/MDTypography";
import React, { useState } from "react";
import { useMutation, useQuery } from "react-query";
import { Formik } from "formik";
import MDButton from "components/MDButton";
import { CreateStrategy, GetSymbol, GetAlgo } from "services/backend_api";
import { Toast } from "utils/constant";
import IsLoad from "services/IsLoad";
import StrategyTable from "./StrategyTable";

function AddStrategyUI() {
  const [refetchData, setRefetchData] = useState(false);
  const mutation = useMutation("CreateStrategy", (values) =>
    CreateStrategy(values.algoId, values.symbolId, values.amount, values.secretKey)
      .then((res) => {
        // console.log(res);
        if (res) {
          if (res.error) {
            Toast.fire({
              icon: "error",
              title: `${res.error}`,
            });
          } else {
            Toast.fire({
              icon: "success",
              title: `Strategy Created Successfully`,
            });
          }
          setRefetchData(true);
          return res;
        }
        return false;
      })
      .catch((e) => {
        Toast.fire({
          icon: "error",
          title: `${e}`,
        });
      })
  );

  const { data: symbolList, isLoadingSym } = useQuery("GetSymbol", () =>
    GetSymbol()
      .then((res) => {
        if (res) {
          if (res.error) {
            Toast.fire({
              icon: "error",
              title: `${res.error}`,
            });
          }
          return res;
        }
        return false;
      })
      .catch((e) => {
        Toast.fire({
          icon: "error",
          title: `${e}`,
        });
      })
  );

  const { data: algoList, isLoading } = useQuery("GetAlgo", () =>
    GetAlgo()
      .then((res) => {
        if (res) {
          if (res.error) {
            Toast.fire({
              icon: "error",
              title: `${res.error}`,
            });
          }
          return res;
        }
        return false;
      })
      .catch((e) => {
        Toast.fire({
          icon: "error",
          title: `${e}`,
        });
      })
  );

  return (
    <>
      <MDBox mt={2}>
        <Grid container>
          <Grid item xs={12} sm={12} md={4} lg={4} p={1} mb={3}>
            <Card style={{ padding: "15px" }}>
              <MDBox
                mx={2}
                mt={-4}
                py={3}
                px={2}
                variant="gradient"
                bgColor="info"
                borderRadius="lg"
                coloredShadow="info"
              >
                <MDTypography variant="h6" color="white">
                  Add Strategy
                </MDTypography>
              </MDBox>
              <Formik
                initialValues={{
                  algoId: "",
                  symbolId: "",
                  amount: "",
                  secretKey: "",
                }}
                validate={(values) => {
                  const errors = {};
                  if (!values.algoId) {
                    errors.algoId = <div style={{ color: "red", fontSize: "12px" }}>Required</div>;
                  }
                  if (!values.symbolId) {
                    errors.symbolId = (
                      <div style={{ color: "red", fontSize: "12px" }}>Required</div>
                    );
                  }
                  if (!values.amount) {
                    errors.amount = <div style={{ color: "red", fontSize: "12px" }}>Required</div>;
                  } else if (values.amount < 1) {
                    errors.amount = (
                      <div style={{ color: "red", fontSize: "12px" }}>Enter Valid Amount</div>
                    );
                  }
                  if (!values.secretKey) {
                    errors.secretKey = (
                      <div style={{ color: "red", fontSize: "12px" }}>Required</div>
                    );
                  }
                  return errors;
                }}
                onSubmit={(values, { resetForm }) => {
                  mutation.mutate(values);
                  resetForm({ values: "" });
                }}
              >
                {({
                  values,
                  errors,
                  touched,
                  handleChange,
                  handleBlur,
                  handleSubmit,
                  /* and other goodies */
                }) => (
                  <MDBox sx={{ width: "100%" }} pl={2} pr={2}>
                    {isLoading ||
                    isLoadingSym ||
                    symbolList === undefined ||
                    algoList === undefined ? (
                      <IsLoad />
                    ) : (
                      <>
                        <MDBox mb={2} mt={4}>
                          <FormControl style={{ width: "100%" }}>
                            <InputLabel id="demo-simple-select-label">Algo ID</InputLabel>
                            <Select
                              sx={{ height: 45 }}
                              IconComponent={() => <ArrowDropDownIcon />}
                              labelId="demo-simple-select-label"
                              label="Algo ID"
                              id="algoId"
                              name="algoId"
                              onChange={(e) => {
                                // eslint-disable-next-line no-param-reassign
                                values.algoId = e.target.value;
                              }}
                              inputProps={{
                                id: "uncontrolled-native",
                              }}
                            >
                              {algoList.map((value) => (
                                <MenuItem value={value.id}>{value.name}</MenuItem>
                              ))}
                            </Select>
                          </FormControl>
                          {errors.algoId && touched.algoId && errors.algoId}
                        </MDBox>
                        <MDBox mb={2} mt={4}>
                          <FormControl style={{ width: "100%" }}>
                            <InputLabel id="demo-simple-select-label">Symbol ID</InputLabel>
                            <Select
                              sx={{ height: 45 }}
                              IconComponent={() => <ArrowDropDownIcon />}
                              labelId="demo-simple-select-label"
                              label="Symbol Type"
                              id="symbolId"
                              name="symbolId"
                              onChange={(e) => {
                                // eslint-disable-next-line no-param-reassign
                                values.symbolId = e.target.value;
                              }}
                              inputProps={{
                                id: "uncontrolled-native",
                              }}
                            >
                              {symbolList.map((value) => (
                                <MenuItem value={value.id}>{value.name}</MenuItem>
                              ))}
                            </Select>
                          </FormControl>
                          {errors.symbolId && touched.symbolId && errors.symbolId}
                        </MDBox>
                      </>
                    )}
                    <MDBox mb={2} mt={3}>
                      <MDInput
                        type="number"
                        label="Amount"
                        name="amount"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.amount}
                        fullWidth
                      />
                      {errors.amount && touched.amount && errors.amount}
                    </MDBox>
                    <MDBox mb={2} mt={3}>
                      <MDInput
                        type="password"
                        label="Secret Key"
                        name="secretKey"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.secretKey}
                        fullWidth
                      />
                      {errors.secretKey && touched.secretKey && errors.secretKey}
                    </MDBox>
                    <MDBox mb={2} mt={3} justifyContent="center" textAlign="center">
                      <MDButton
                        variant="gradient"
                        type="submit"
                        color="info"
                        onClick={() => {
                          handleSubmit();
                        }}
                      >
                        Add Strategy
                      </MDButton>
                    </MDBox>
                  </MDBox>
                )}
              </Formik>
            </Card>
          </Grid>
          <Grid item xs={12} sm={12} md={8} lg={8} p={1}>
            <Card style={{ padding: "15px" }}>
              <StrategyTable refetchData={refetchData} setRefetchData={setRefetchData} />
            </Card>
          </Grid>
        </Grid>
      </MDBox>
    </>
  );
}

export default AddStrategyUI;
