/* eslint-disable no-use-before-define */
/* eslint-disable dot-notation */
/* eslint-disable no-multi-assign */
/* eslint-disable react/no-this-in-sfc */
/* eslint-disable no-sequences */
/* eslint-disable react/self-closing-comp */
/* eslint-disable no-useless-return */
/* eslint-disable import/no-unresolved */
// @mui material components
import Grid from "@mui/material/Grid";
import Card from "@mui/material/Card";
import React, { useState } from "react";
import useCollapse from "react-collapsed";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import KeyboardArrowUpIcon from "@mui/icons-material/KeyboardArrowUp";
import "./reportStyle.css";
import { useLocation, useNavigate } from "react-router-dom";
// Algo Craze components
import MDBox from "components/MDBox";
import { Checkbox } from "@mui/material";
import MDTypography from "components/MDTypography";
import DashboardLayout from "examples/LayoutContainers/DashboardLayout";
import DashboardNavbar from "examples/Navbars/DashboardNavbar";
import DataTable from "examples/Tables/DataTable";
import MDButton from "components/MDButton";
import { Report } from "services/backend_api";
import { useQuery } from "react-query";
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";
import IsLoad from "services/IsLoad";
import { REPORTCSV, REPORTPDF, MANAGEUSERS } from "utils/constant";
import CustomDatePicker from "./CustomDatePicker";
import MultipleSelectCheckmarks from "./multipleSelect";
import CustomTimePicker from "./CustomTimePicker";
import CustomTypeSelect from "./CustomTypeSelect";
import CustomActionSelect from "./CustomActionSelect";

function Reports() {
  const { state } = useLocation();
  const { uid } = state;

  const [sdata, setData] = useState([]);
  const navigate = useNavigate();
  const redirectCSV = () => {
    const path = REPORTCSV;
    navigate(path, { state: { sdata, uid } });
  };

  const redirectPDF = () => {
    const path = REPORTPDF;
    navigate(path, { state: { sdata, uid } });
  };

  const { getCollapseProps, getToggleProps, isExpanded } = useCollapse();

  const [checkOne, setCheckOne] = useState(false);
  const [checkTwo, setCheckTwo] = useState(false);
  const [checkThree, setCheckThree] = useState(false);
  const [checkSix, setCheckSix] = useState(false);
  const [checkSeven, setCheckSeven] = useState(false);

  const [day, setDay] = useState([]);
  const [date, setDate] = useState();
  const [time, setTime] = useState();
  const [type, setType] = useState("");
  const [action, setAction] = useState("");
  const [refresh, setRefresh] = useState(true);
  // const data = {
  //   day: day.value,
  //   date,
  //   time,
  //   type,
  //   action,
  // };
  // console.log("data : ", data);
  const clearFunc = () => {
    setRefresh(false);
    setCheckOne(false);
    setCheckTwo(false);
    setCheckThree(false);
    setCheckSix(false);
    setCheckSeven(false);
    setDay([]);
    setDate();
    setTime();
    setType("");
    setAction("");
    refetch();
    setTimeout(() => {
      setRefresh(true);
    }, 50);
  };

  const showBtn = () => {
    // console.log("inside")
    refetch();
  };

  // eslint-disable-next-line no-unused-vars
  const { data, refetch, isLoading } = useQuery("Report", () =>
    Report(
      day.value ? day.value : [],
      type,
      action,
      date ? date.start.toString() : "",
      date ? date.end.toString() : "",
      time ? time.start.toString() : "",
      time ? time.end.toString() : "",
      state.uid
    ).then((res) => {
      if (res) {
        setData(res);
        return res;
      }
      return false;
    })
  );

  return (
    <>
      <DashboardLayout>
        <DashboardNavbar />
        <MDBox pt={2}>
          <MDBox mt={-3} pt={3} pb={2}>
            <MDButton
              variant="gradient"
              color="info"
              circular
              iconOnly
              style={{ borderRadius: 100 }}
              onClick={() => navigate(MANAGEUSERS)}
            >
              <ArrowBackIosIcon fontSize="small" style={{ position: "absolute", right: 8.5 }} />
            </MDButton>
          </MDBox>
        </MDBox>
        <MDBox
          position="relative"
          mt={1}
          py={3}
          px={2}
          style={{ cursor: "pointer", display: "flex", alignItems: "center" }}
          variant="gradient"
          bgColor="info"
          borderRadius="lg"
          coloredShadow="info"
          {...getToggleProps()}
        >
          <MDTypography variant="h6" color="white">
            Filters
          </MDTypography>
          {isExpanded ? (
            <KeyboardArrowUpIcon
              style={{ color: "white", paddingLeft: "5px", transform: "scale(2)" }}
            />
          ) : (
            <KeyboardArrowDownIcon
              style={{ color: "white", paddingLeft: "5px", transform: "scale(2)" }}
            />
          )}
        </MDBox>
        <MDBox {...getCollapseProps()}>
          <Card
            style={{ marginTop: "10px" }}
            sx={{
              position: "relative",
              mt: -8,
              py: 2,
              px: 2,
            }}
          >
            {refresh ? (
              <>
                <Grid container spacing={3} className="gridContainer">
                  <Grid
                    item
                    xs={12}
                    md={2}
                    lg={2}
                    style={{ display: "flex", alignItems: "center" }}
                  >
                    <Checkbox checked={checkOne} onChange={() => setCheckOne(!checkOne)} />
                    <MDTypography
                      variant=""
                      onClick={() => setCheckOne(!checkOne)}
                      fontWeight="regular"
                      sx={{ margin: "0 20px", cursor: "pointer" }}
                    >
                      Day
                    </MDTypography>
                  </Grid>
                  <Grid item xs={12} md={8} lg={8} className="gridCol gridMobile">
                    <MultipleSelectCheckmarks id={checkOne} name="Day" setDay={setDay} />
                  </Grid>
                </Grid>
                <Grid container spacing={3} className="gridContainer">
                  <Grid item xs={12} md={2} lg={2} className="gridItem">
                    <Checkbox checked={checkTwo} onChange={() => setCheckTwo(!checkTwo)} />
                    <MDTypography
                      variant=""
                      onClick={() => setCheckTwo(!checkTwo)}
                      fontWeight="regular"
                      sx={{ margin: "0 20px", cursor: "pointer" }}
                    >
                      Date
                    </MDTypography>
                  </Grid>
                  <Grid item xs={12} md={8} lg={8} className="gridItem gridCol">
                    <CustomDatePicker id={checkTwo} name="Date" setDate={setDate} />
                  </Grid>
                </Grid>
                <Grid container spacing={3} className="gridContainer">
                  <Grid item xs={12} md={2} lg={2} className="gridItem">
                    <Checkbox checked={checkThree} onChange={() => setCheckThree(!checkThree)} />
                    <MDTypography
                      variant=""
                      onClick={() => setCheckThree(!checkThree)}
                      fontWeight="regular"
                      sx={{ margin: "0 20px", cursor: "pointer" }}
                    >
                      Time
                    </MDTypography>
                  </Grid>
                  <Grid item xs={12} md={8} lg={8} className="gridItem gridCol">
                    <CustomTimePicker id={checkThree} setTime={setTime} />
                  </Grid>
                </Grid>
                <Grid container spacing={3} className="gridContainer">
                  <Grid item xs={12} md={2} lg={2} className="gridItem">
                    <Checkbox checked={checkSix} onChange={() => setCheckSix(!checkSix)} />
                    <MDTypography
                      variant=""
                      onClick={() => setCheckSix(!checkSix)}
                      fontWeight="regular"
                      sx={{ margin: "0 20px", cursor: "pointer" }}
                    >
                      Type
                    </MDTypography>
                  </Grid>
                  <Grid item xs={12} md={8} lg={8} className="gridItem gridCol">
                    <CustomTypeSelect id={checkSix} setType={setType} />
                  </Grid>
                </Grid>
                <Grid container spacing={3} className="gridContainer">
                  <Grid item xs={12} md={2} lg={2} className="gridItem">
                    <Checkbox checked={checkSeven} onChange={() => setCheckSeven(!checkSeven)} />
                    <MDTypography
                      variant=""
                      onClick={() => setCheckSeven(!checkSeven)}
                      fontWeight="regular"
                      sx={{ margin: "0 20px", cursor: "pointer" }}
                    >
                      Action
                    </MDTypography>
                  </Grid>
                  <Grid item xs={12} md={8} lg={8} className="gridItem gridCol">
                    <CustomActionSelect id={checkSeven} setAction={setAction} />
                  </Grid>
                </Grid>
              </>
            ) : (
              <IsLoad isLoad={refresh !== true} />
            )}
            <Grid
              container
              spacing={3}
              className="gridContainer"
              style={{ display: "flex", alignItems: "center", justifyContent: "center" }}
            >
              <MDBox
                m={5}
                className="btnContainer"
                style={{ display: "flex", alignItems: "center" }}
              >
                <MDButton
                  variant="gradient"
                  color="error"
                  type="button"
                  onClick={clearFunc}
                  style={{ marginRight: 5 }}
                  fullWidth
                >
                  Clear
                </MDButton>
                <MDButton
                  variant="gradient"
                  color="success"
                  type="button"
                  onClick={showBtn}
                  style={{ marginRight: 5 }}
                  fullWidth
                >
                  Show
                </MDButton>
                <MDButton
                  variant="gradient"
                  color="info"
                  type="button"
                  onClick={redirectCSV}
                  style={{ marginRight: 5 }}
                  fullWidth
                >
                  CSV
                </MDButton>
                <MDButton
                  variant="gradient"
                  color="info"
                  type="button"
                  onClick={redirectPDF}
                  style={{ marginRight: 5 }}
                  fullWidth
                >
                  PDF
                </MDButton>
              </MDBox>
            </Grid>
          </Card>
        </MDBox>
        <Grid item xs={12} mt={2}>
          <Card>
            <MDBox pt={3}>
              {isLoading ? (
                <IsLoad isLoad={isLoading} />
              ) : (
                <DataTable
                  table={{
                    columns: [
                      { Header: "Number", accessor: "number", align: "left" },
                      { Header: "Strategy", accessor: "strategy", align: "left" },
                      { Header: "Script", accessor: "script", align: "left" },
                      { Header: "Type", accessor: "type", align: "left" },
                      { Header: "Date", accessor: "date", align: "left" },
                      { Header: "Time", accessor: "time", align: "left" },
                      { Header: "Action", accessor: "action", align: "left" },
                      { Header: "PNL", accessor: "pnl", align: "center" },
                    ],

                    rows: sdata.map((value, ind) => {
                      // eslint-disable-next-line prefer-const
                      let x = {};
                      // const d_id = value["id"]
                      x["number"] = 1 + ind;
                      x["strategy"] = value["strategy"];
                      x["script"] = value["script"];
                      x["type"] = value["type"];
                      x["date"] = value["date"];
                      x["time"] = value["time"];
                      x["action"] = value["action"];
                      x["pnl"] = value["pnl"];
                      return x;
                    }),
                  }}
                  noEndBorder
                />
              )}
            </MDBox>
          </Card>
        </Grid>
      </DashboardLayout>
    </>
  );
}
export default Reports;
