/* eslint-disable dot-notation */
import React, { useState } from "react";
import DataTable from "examples/Tables/DataTable";
import IsLoad from "services/IsLoad";
import DeleteIcon from "@mui/icons-material/Delete";
import { useMutation, useQuery } from "react-query";
import {
  GetUser,
  UpdateUserTradingStatus,
  DeleteUser,
  GetAgentUserList,
} from "services/backend_api";
import MDBox from "components/MDBox";
import MDTypography from "components/MDTypography";
import MDButton from "components/MDButton";
import { Card, Switch } from "@mui/material";
import { Toast, USERDETAILTABLE, BROKERINTEGRATION, REPORT, USERACTIVITY } from "utils/constant";
import { useLocation, useNavigate } from "react-router-dom";
// Dialog box componants
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";

function UsersTable() {
  const { state } = useLocation();
  const [currentRowId, setcurrentRowId] = useState(0);
  const [open, setOpen] = useState(false);
  const handleClose = () => {
    setOpen(false);
  };
  const handleClickOpen = (id) => {
    setcurrentRowId(id);
    setOpen(true);
  };
  const navigate = useNavigate();
  const routeChange = (uid, name, email, mobile, pnl, totalSub, agentName) => {
    const path = USERDETAILTABLE;
    navigate(path, {
      state: { uid, name, email, mobile, pnl, totalSub, agentName },
    });
  };
  const brokerRouteChange = (uID) => {
    const path = BROKERINTEGRATION;
    navigate(path, { state: { uid: uID } });
  };
  const reportRouteChange = (uid) => {
    const path = REPORT;
    navigate(path, { state: { uid } });
  };
  const useractivityRouteChange = (uid) => {
    const path = USERACTIVITY;
    navigate(path, { state: { uid } });
  };
  let useQueryCallName = "";
  let callableFunction = null;
  if (state) {
    useQueryCallName = "GetAgentUserList";
    callableFunction = GetAgentUserList(state.agentid);
  } else {
    useQueryCallName = "GetUser";
    callableFunction = GetUser();
  }
  const { data, refetch, isLoading } = useQuery(useQueryCallName, () =>
    callableFunction
      .then((res) => {
        if (res) {
          if (res.error) {
            Toast.fire({
              icon: "error",
              title: `${res.error}`,
            });
          }
          return res;
        }
        return false;
      })
      .catch((e) => {
        Toast.fire({
          icon: "error",
          title: `${e}`,
        });
      })
  );

  const liveTradingMutate = useMutation("UpdateUserTradingStatus", (values) =>
    UpdateUserTradingStatus(values.uid, values.status)
      .then((res) => {
        if (res) {
          if (res.error) {
            Toast.fire({
              icon: "error",
              title: `${res.error}`,
            });
          } else {
            Toast.fire({
              icon: "success",
              title: "Live Trading Status Changed",
            });
          }
          refetch();
          return res;
        }
        return false;
      })
      .catch((e) => {
        Toast.fire({
          icon: "error",
          title: `${e}`,
        });
      })
  );
  const mutation = useMutation("DeleteUser", (values) =>
    DeleteUser(values)
      .then((res) => {
        if (res) {
          if (res.error) {
            Toast.fire({
              icon: "error",
              title: `${res.error}`,
            });
          } else {
            Toast.fire({
              icon: "success",
              title: `User Deleted Successfully`,
            });
          }
          refetch();
          return res;
        }
        return false;
      })
      .catch((e) => {
        Toast.fire({
          icon: "error",
          title: `${e}`,
        });
      })
  );
  const deletedata = (id) => {
    mutation.mutate(id);
    setOpen(false);
  };
  const handleTradingStatus = (uid, status) => {
    let value;
    if (!status) {
      value = { uid, status: "on" };
    } else {
      value = { uid, status: "off_by_admin" };
    }
    liveTradingMutate.mutate(value);
  };

  return (
    <>
      <MDBox mt={5}>
        <Card style={{ padding: "15px" }}>
          <MDBox
            mx={2}
            mt={-6}
            py={3}
            px={2}
            variant="gradient"
            bgColor="info"
            borderRadius="lg"
            coloredShadow="info"
          >
            <MDTypography variant="h6" color="white">
              Users List
            </MDTypography>
          </MDBox>
          <MDBox>
            {isLoading || data === undefined ? (
              <IsLoad />
            ) : (
              <DataTable
                table={{
                  columns: [
                    { Header: "id", accessor: "id", align: "center" },
                    { Header: "Name", accessor: "username", align: "center" },
                    { Header: "detail", accessor: "detail", align: "center" },
                    {
                      Header: "Broker Integrated",
                      accessor: "brokers_integrated",
                      align: "center",
                    },
                    { Header: "Trading Status", accessor: "trading", align: "center" },
                    { Header: "Report", accessor: "report", align: "center" },
                    { Header: "Date", accessor: "create_date", align: "center" },
                    { Header: "Activity", accessor: "activity", align: "center" },
                    { Header: "action", accessor: "action", align: "center" },
                  ],
                  rows: data.map((value, ind) => {
                    // eslint-disable-next-line prefer-const
                    let x = {};
                    x["id"] = 1 + ind;
                    x["username"] = value["username"];
                    x["detail"] = (
                      <MDButton
                        variant="gradient"
                        color="info"
                        size="small"
                        fullWidth
                        onClick={() => {
                          routeChange(
                            value["id"],
                            value["username"],
                            value["email"],
                            value["mobile"],
                            value["total_pnl"],
                            value["total_sub"],
                            value["agent_name"]
                          );
                        }}
                      >
                        Show
                      </MDButton>
                    );
                    x["brokers_integrated"] = (
                      <MDBox ml={-1}>
                        <MDButton
                          variant="gradient"
                          color={value["brokers_integrated"] ? "secondary" : "success"}
                          size="small"
                          fullWidth
                          onClick={() => {
                            brokerRouteChange(value["id"]);
                          }}
                        >
                          {value["brokers_integrated"] ? "Integrated" : "Not Integrated"}
                        </MDButton>
                      </MDBox>
                    );
                    let tradingState = false;
                    // eslint-disable-next-line eqeqeq
                    if (value["trading"] == "on") {
                      tradingState = true;
                    }
                    x["trading"] = (
                      <Switch
                        sx={{ marginLeft: "10px" }}
                        checked={tradingState}
                        onChange={() => {
                          handleTradingStatus(value["id"], tradingState);
                        }}
                      />
                    );
                    x["report"] = (
                      <MDButton
                        variant="gradient"
                        color="info"
                        size="small"
                        fullWidth
                        onClick={() => {
                          reportRouteChange(value["id"]);
                        }}
                      >
                        Show
                      </MDButton>
                    );
                    x["create_date"] = value["create_date"];
                    x["activity"] = (
                      <MDButton
                        variant="gradient"
                        color="info"
                        size="small"
                        fullWidth
                        onClick={() => {
                          useractivityRouteChange(value["id"]);
                        }}
                      >
                        view
                      </MDButton>
                    );
                    x["action"] = (
                      <>
                        <MDButton>
                          <DeleteIcon
                            variant="gradient"
                            color="error"
                            aria-label="delete"
                            sx={{
                              fontWeight: "bold",
                              cursor: "pointer",
                              transform: "scale(1.5)",
                            }}
                            onClick={() => {
                              handleClickOpen(value["id"]);
                            }}
                          >
                            Delete
                          </DeleteIcon>
                        </MDButton>
                        <Dialog
                          open={open}
                          onClose={handleClose}
                          aria-labelledby="alert-dialog-title"
                          aria-describedby="alert-dialog-description"
                        >
                          <DialogTitle id="alert-dialog-title">Information</DialogTitle>
                          <DialogContent>
                            <DialogContentText id="alert-dialog-description">
                              Are you sure you want to delete
                            </DialogContentText>
                          </DialogContent>
                          <DialogActions>
                            <MDButton onClick={handleClose}>No</MDButton>
                            <MDButton
                              onClick={() => {
                                deletedata(currentRowId);
                              }}
                            >
                              Yes
                            </MDButton>
                          </DialogActions>
                        </Dialog>
                      </>
                    );
                    return x;
                  }),
                }}
              />
            )}
          </MDBox>
        </Card>
      </MDBox>
    </>
  );
}

export default UsersTable;
