// import { useNavigate } from "react-router-dom";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import ArrowDropDownIcon from "@mui/icons-material/ArrowDropDown";
import Select from "@mui/material/Select";
// Algo Craze components
import Card from "@mui/material/Card";
import MDBox from "components/MDBox";
import MDTypography from "components/MDTypography";
import { Grid } from "@mui/material";
import MDInput from "components/MDInput";
import { Formik } from "formik";
import MDButton from "components/MDButton";
import { useMutation } from "react-query";
import { Toast, SIGNIN } from "utils/constant";
import DashboardLayout from "examples/LayoutContainers/DashboardLayout";
import DashboardNavbar from "examples/Navbars/DashboardNavbar";
import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { CreateAgent, CreateUser } from "services/backend_api";

function getPageContent() {
  const [type, setType] = useState("user");
  const mutation = useMutation("CreateAgent", (values) =>
    CreateAgent(values.name, values.email, values.password, values.phone.toString())
      .then((res) => {
        if (res) {
          if (res.error) {
            Toast.fire({
              icon: "error",
              title: `${res.error}`,
            });
          } else {
            Toast.fire({
              icon: "success",
              title: "Agent added successfully",
            });
          }
          return res;
        }
        return false;
      })
      .catch((e) => {
        Toast.fire({
          icon: "error",
          title: `${e}`,
        });
      })
  );

  const usermutation = useMutation("CreateUser", (values) =>
    CreateUser(values.name, values.email, values.password, values.phone.toString())
      .then((res) => {
        if (res) {
          if (res.error) {
            Toast.fire({
              icon: "error",
              title: `${res.error}`,
            });
          } else {
            Toast.fire({
              icon: "success",
              title: "User added successfully",
            });
          }
          return res;
        }
        return false;
      })
      .catch((e) => {
        Toast.fire({
          icon: "error",
          title: `${e}`,
        });
      })
  );
  const handleOnChange = (event) => {
    setType(event.target.value);
  };
  return (
    <>
      <DashboardLayout>
        <DashboardNavbar />
        <MDBox mt={3} mb={2}>
          <Card mt={5}>
            <MDTypography variant="h3" mt={3} pl={4} pr={4} fontWeight="medium" textAlign="center">
              Register {type.toUpperCase()}
            </MDTypography>
            <MDBox mt={5}>
              {/* Dropdown start */}
              <Grid container spacing={2} ml={4} pr={4} mt={2}>
                <FormControl md={3} sm={12} lg={4} pr={3}>
                  <InputLabel id="demo-simple-select-label">Register</InputLabel>
                  <Select
                    sx={{ height: 45, width: 250 }}
                    IconComponent={() => <ArrowDropDownIcon />}
                    labelId="demo-simple-select-label"
                    label="Register"
                    id="demo-simple-select"
                    onChange={handleOnChange}
                    defaultValue={type}
                    inputProps={{
                      name: "type",
                      id: "uncontrolled-native",
                    }}
                  >
                    <MenuItem value="user">User</MenuItem>
                    <MenuItem value="agent">Agent</MenuItem>
                  </Select>
                </FormControl>
              </Grid>
            </MDBox>
            <Formik
              initialValues={{
                name: "",
                email: "",
                phone: "",
                password: "",
                passwordConfirmation: "",
              }}
              validate={(values) => {
                const errors = {};
                if (!values.email) {
                  errors.email = <div style={{ color: "red", fontSize: "12px" }}>Required</div>;
                } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)) {
                  errors.email = (
                    <div style={{ color: "red", fontSize: "12px" }}>Invalid email address</div>
                  );
                }

                if (!values.name) {
                  errors.name = <div style={{ color: "red", fontSize: "12px" }}>Required</div>;
                } else if (values.name.length > 50) {
                  errors.name = (
                    <div style={{ color: "red", fontSize: "12px" }}>
                      Must be 50 characters or less
                    </div>
                  );
                }

                if (!values.phone) {
                  errors.phone = <div style={{ color: "red", fontSize: "12px" }}>Required</div>;
                } else if (values.phone.toString().length > 10) {
                  errors.phone = (
                    <div style={{ color: "red", fontSize: "12px" }}>Phone number is not valid</div>
                  );
                } else if (values.phone.toString().length < 10) {
                  errors.phone = (
                    <div style={{ color: "red", fontSize: "12px" }}>Phone number is not valid</div>
                  );
                }

                if (!values.password) {
                  errors.password = <div style={{ color: "red", fontSize: "12px" }}>Required</div>;
                } else if (values.password.length < 5) {
                  errors.password = (
                    <div style={{ color: "red", fontSize: "12px" }}>
                      Must be more than 5 Character
                    </div>
                  );
                } else if (values.password !== values.passwordConfirmation) {
                  errors.passwordConfirmation = (
                    <div style={{ color: "red", fontSize: "12px" }}>Password not matched</div>
                  );
                }
                return errors;
              }}
              onSubmit={(values, { resetForm }) => {
                // console.log(values, type);
                if (type === "user") {
                  usermutation.mutate(values);
                } else {
                  mutation.mutate(values);
                }
                resetForm({ values: "" });
              }}
            >
              {({
                values,
                errors,
                touched,
                handleChange,
                handleBlur,
                handleSubmit,
                resetForm,
                /* and other goodies */
              }) => (
                <MDBox sx={{ width: "100%" }}>
                  <Grid container mt={5} pl={4} pr={4} display="flex">
                    <Grid item xs={12} sm={6} md={4} lg={4} mb={2} pr={3}>
                      <MDBox mb={2}>
                        <MDInput
                          type="text"
                          label="Name"
                          name="name"
                          onChange={handleChange}
                          onBlur={handleBlur}
                          value={values.name}
                          fullWidth
                        />
                        {errors.name && touched.name && errors.name}
                      </MDBox>
                    </Grid>
                    <Grid item xs={12} sm={6} md={4} lg={4} mb={2} pr={3}>
                      <MDBox mb={2}>
                        <MDInput
                          type="number"
                          label="Phone No"
                          name="phone"
                          onChange={handleChange}
                          onBlur={handleBlur}
                          value={values.phone}
                          fullWidth
                        />
                        {errors.phone && touched.phone && errors.phone}
                      </MDBox>
                    </Grid>
                    <Grid item xs={12} sm={6} md={4} lg={4} mb={2} pr={3}>
                      <MDBox mb={2}>
                        <MDInput
                          type="email"
                          label="Email"
                          name="email"
                          onChange={handleChange}
                          onBlur={handleBlur}
                          value={values.email}
                          fullWidth
                        />
                        {errors.email && touched.email && errors.email}
                      </MDBox>
                    </Grid>
                  </Grid>
                  <Grid container mt={1} pl={4} pr={4} display="flex">
                    <Grid item xs={12} sm={6} md={4} lg={4} mb={2} pr={3}>
                      <MDBox mb={2}>
                        <MDInput
                          type="password"
                          label="Password"
                          name="password"
                          onChange={handleChange}
                          onBlur={handleBlur}
                          value={values.password}
                          fullWidth
                        />
                        {errors.password && touched.password && errors.password}
                      </MDBox>
                    </Grid>
                    <Grid item xs={12} sm={6} md={4} lg={4} mb={2} pr={3}>
                      <MDBox mb={2}>
                        <MDInput
                          type="password"
                          label="Confirm password"
                          name="passwordConfirmation"
                          onChange={handleChange}
                          onBlur={handleBlur}
                          value={values.passwordConfirmation}
                          fullWidth
                        />
                        {errors.passwordConfirmation &&
                          touched.passwordConfirmation &&
                          errors.passwordConfirmation}
                      </MDBox>
                    </Grid>
                  </Grid>
                  <Grid
                    container
                    mt={1}
                    mb={4}
                    pl={4}
                    pr={4}
                    className="btnContainer"
                    display="flex"
                    alignItems="center"
                    justifyContent="center"
                  >
                    <MDBox pr={3}>
                      <MDButton
                        variant="gradient"
                        type="submit"
                        color="info"
                        onClick={() => {
                          handleSubmit();
                        }}
                        fullWidth
                      >
                        Add {type}
                      </MDButton>
                    </MDBox>
                    <MDBox pl={3}>
                      <MDButton
                        type="reset"
                        variant="gradient"
                        color="error"
                        onClick={resetForm}
                        fullWidth
                      >
                        Clear
                      </MDButton>
                    </MDBox>
                  </Grid>
                </MDBox>
              )}
            </Formik>
          </Card>
        </MDBox>
      </DashboardLayout>
    </>
  );
}

function add() {
  const sessionId = localStorage.getItem("admin_session_id");
  const navigate = useNavigate();
  if (!sessionId) {
    useEffect(() => {
      if (!sessionId) {
        navigate(SIGNIN);
      }
    }, []);
  } else {
    return getPageContent();
  }
  return <></>;
}

export default add;
