/** 
  All of the routes for the Algocraze Dashboard React are added here,
  You can add a new route, customize the routes and delete the routes here.

  Once you add a new route on this file it will be visible automatically on
  the Sidenav.

  For adding a new route you can follow the existing routes in the routes array.
  1. The `type` key with the `collapse` value is used for a route.
  2. The `type` key with the `title` value is used for a title inside the Sidenav. 
  3. The `type` key with the `divider` value is used for a divider between Sidenav items.
  4. The `name` key is used for the name of the route on the Sidenav.
  5. The `key` key is used for the key of the route (It will help you with the key prop inside a loop).
  6. The `icon` key is used for the icon of the route on the Sidenav, you have to add a node.
  7. The `collapse` key is used for making a collapsible item on the Sidenav that has other routes
  inside (nested routes), you need to pass the nested routes inside an array as a value for the `collapse` key.
  8. The `route` key is used to store the route location which is used for the react router.
  9. The `href` key is used to store the external links location.
  10. The `title` key is only for the item with the type of `title` and its used for the title text on the Sidenav.
  10. The `component` key is used to store the component of its route.
*/

// Algo Craze layouts
import Dashboard from "layouts/dashboard";
// import Profile from "layouts/profile";
import SignIn from "layouts/authentication/sign-in";
// import SignUp from "layouts/authentication/sign-up";
import Error from "layouts/page_not_found";
// @mui icons
import Icon from "@mui/material/Icon";
import Add from "layouts/add";
import AddSymbol from "layouts/add_symbol";
import {
  DASHBOARD,
  DEFAULT,
  ADD_UA,
  ADDSYMBOL,
  ADDALGO,
  ADDSTRATEGY,
  MANAGEAGENTS,
  MANAGEUSERS,
  // SHOWAGENTSUSER,
  SIGNAL,
  // DATAFEEDCONF,
  LOGS,
  BROKERINTEGRATION,
  USERDETAILTABLE,
  REPORT,
  REPORTCSV,
  REPORTPDF,
  SIGNIN,
  USERACTIVITY,
  AGENTACTIVITY,
} from "utils/constant";
import AddAlgo from "layouts/add_algo";
import AddStrategy from "layouts/add_strategy";
import ManageAgents from "layouts/manage_agents";
import ManageUsers from "layouts/manage_users";
// import ShowUser from "layouts/manage_agents/ShowUser";
import Signal from "layouts/signal";
// import DataFeedConf from "layouts/data_feed_conf";
import Logs from "layouts/activity_logs";
import UserBrokerIntegration from "layouts/manage_users/broker_integration";
import UserDetailTable from "layouts/manage_users/UserDetailTable";
import Reports from "layouts/manage_users/reports";
import ReportCSV from "layouts/manage_users/reports/ReportCSV";
import ReportPDF from "layouts/manage_users/reports/ReportPDF";
import UserActivityLogs from "layouts/manage_users/user_log";
import AgentActivityLogs from "layouts/manage_agents/agent_log";

const routes = [
  {
    type: "collapse",
    name: "Dashboard",
    key: "dashboard",
    icon: <Icon fontSize="small">dashboard</Icon>,
    route: DASHBOARD,
    component: <Dashboard />,
  },
  {
    type: "collapse",
    name: "Add",
    key: "add",
    icon: <Icon fontSize="small">personadd</Icon>,
    route: ADD_UA,
    component: <Add />,
  },
  {
    type: "collapse",
    name: "Add Symbol",
    key: "symbol",
    icon: <Icon fontSize="small">dashboard</Icon>,
    route: ADDSYMBOL,
    component: <AddSymbol />,
  },
  {
    type: "collapse",
    name: "Add Algo",
    key: "algo",
    icon: <Icon fontSize="small">dashboard</Icon>,
    route: ADDALGO,
    component: <AddAlgo />,
  },
  {
    type: "collapse",
    name: "Add Strategy",
    key: "strategy",
    icon: <Icon fontSize="small">dashboard</Icon>,
    route: ADDSTRATEGY,
    component: <AddStrategy />,
  },
  {
    type: "collapse",
    name: "Manage Agents",
    key: "manageagents",
    icon: <Icon fontSize="small">personadd</Icon>,
    route: MANAGEAGENTS,
    component: <ManageAgents />,
  },
  {
    type: "collapse",
    name: "Manage Users",
    key: "manageusers",
    icon: <Icon fontSize="small">personadd</Icon>,
    route: MANAGEUSERS,
    component: <ManageUsers />,
  },
  // {
  //   type: "collapse",
  //   name: "Data Feed Conf",
  //   key: "datafeedconf",
  //   icon: <Icon fontSize="small">personadd</Icon>,
  //   route: DATAFEEDCONF,
  //   component: <DataFeedConf />,
  // },
  {
    type: "collapse",
    name: "Signal",
    key: "signal",
    icon: <Icon fontSize="small">dashboard</Icon>,
    route: SIGNAL,
    component: <Signal />,
  },
  {
    type: "collapse",
    name: "Logs",
    key: "logs",
    icon: <Icon fontSize="small">dashboard</Icon>,
    route: LOGS,
    component: <Logs />,
  },
  // {
  //   type: "collapse",
  //   name: "Profile",
  //   key: "profile",
  //   icon: <Icon fontSize="small">person</Icon>,
  //   route: "/profile",
  //   component: <Profile />,
  // },
  // {
  //   key: "showuser",
  //   route: SHOWAGENTSUSER,
  //   component: <ShowUser />,
  // },
  {
    key: "brokerintegration",
    route: BROKERINTEGRATION,
    component: <UserBrokerIntegration />,
  },
  {
    key: "userreport",
    route: REPORT,
    component: <Reports />,
  },
  {
    key: "reportcsv",
    route: REPORTCSV,
    component: <ReportCSV />,
  },
  {
    key: "reportpdf",
    route: REPORTPDF,
    component: <ReportPDF />,
  },
  {
    key: "userdetails",
    route: USERDETAILTABLE,
    component: <UserDetailTable />,
  },
  {
    key: "signin",
    route: SIGNIN,
    component: <SignIn />,
  },
  {
    key: "Default",
    route: DEFAULT,
    component: <SignIn />,
  },
  {
    key: "Error",
    route: "*",
    component: <Error />,
  },
  {
    key: "useractivity",
    route: USERACTIVITY,
    component: <UserActivityLogs />,
  },
  {
    key: "agentactivity",
    route: AGENTACTIVITY,
    component: <AgentActivityLogs />,
  },
];

export default routes;
