/* eslint-disable dot-notation */
import React, { useState } from "react";
import DataTable from "examples/Tables/DataTable";
import IsLoad from "services/IsLoad";
import { useMutation, useQuery } from "react-query";
import { GetAgent, DeleteAgent } from "services/backend_api";
import MDBox from "components/MDBox";
import MDTypography from "components/MDTypography";
import MDButton from "components/MDButton";
import DeleteIcon from "@mui/icons-material/Delete";
// import { Toast } from "utils/constant";
import { Card } from "@mui/material";
import { useNavigate } from "react-router-dom";
import { MANAGEUSERS, Toast, AGENTACTIVITY } from "utils/constant";
// Dialog box componants
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";

function AgentsTable() {
  const [currentRowId, setcurrentRowId] = useState(0);
  const [open, setOpen] = useState(false);
  const handleClose = () => {
    setOpen(false);
  };
  const handleClickOpen = (id) => {
    setcurrentRowId(id);
    setOpen(true);
  };
  const navigate = useNavigate();

  const agentActivityRouteChange = (uid) => {
    const path = AGENTACTIVITY;
    navigate(path, { state: { uid } });
  };
  const { data, refetch, isLoading } = useQuery("GetAgent", () =>
    GetAgent()
      .then((res) => {
        if (res) {
          if (res.error) {
            Toast.fire({
              icon: "error",
              title: `${res.error}`,
            });
          }
          return res;
        }
        return false;
      })
      .catch((e) => {
        Toast.fire({
          icon: "error",
          title: `${e}`,
        });
      })
  );

  const mutation = useMutation("DeleteAgent", (values) =>
    DeleteAgent(values)
      .then((res) => {
        if (res) {
          if (res.error) {
            Toast.fire({
              icon: "error",
              title: `${res.error}`,
            });
          } else {
            Toast.fire({
              icon: "success",
              title: `Agent Deleted Successfully`,
            });
          }
          refetch();
          return res;
        }
        return false;
      })
      .catch((e) => {
        Toast.fire({
          icon: "error",
          title: `${e}`,
        });
      })
  );
  const deletedata = (id) => {
    mutation.mutate(id);
    setOpen(false);
  };
  const routeChange = (agentID, agentName) => {
    const path = MANAGEUSERS;
    navigate(path, { state: { agentid: agentID, agentname: agentName } });
  };
  return (
    <>
      <MDBox mt={5}>
        <Card style={{ padding: "15px" }}>
          <MDBox
            mx={2}
            mt={-6}
            py={3}
            px={2}
            variant="gradient"
            bgColor="info"
            borderRadius="lg"
            coloredShadow="info"
          >
            <MDTypography variant="h6" color="white">
              Agent List
            </MDTypography>
          </MDBox>
          <MDBox>
            {isLoading || data === undefined ? (
              <IsLoad />
            ) : (
              <DataTable
                table={{
                  columns: [
                    { Header: "id", accessor: "id", align: "center" },
                    { Header: "Name", accessor: "name", align: "center" },
                    { Header: "Email", accessor: "email", align: "center" },
                    { Header: "Mobile No", accessor: "mobile", align: "center" },
                    { Header: "No. Of Users", accessor: "number_of_users", align: "center" },
                    { Header: "Users", accessor: "user", align: "center" },
                    { Header: "Date", accessor: "date", align: "center" },
                    { Header: "Activity", accessor: "activity", align: "center" },
                    { Header: "action", accessor: "action", align: "center" },
                  ],
                  rows: data.map((value, ind) => {
                    // eslint-disable-next-line prefer-const
                    let x = {};
                    x["id"] = 1 + ind;
                    x["name"] = value["name"];
                    x["email"] = value["email"];
                    x["mobile"] = value["mobile"];
                    x["number_of_users"] = value["number_of_users"];
                    x["date"] = value["create_date"];
                    x["user"] = (
                      <MDButton
                        variant="gradient"
                        color="success"
                        size="small"
                        fullWidth
                        onClick={() => {
                          routeChange(value["id"], value["name"]);
                        }}
                      >
                        Show User
                      </MDButton>
                    );
                    x["activity"] = (
                      <MDButton
                        variant="gradient"
                        color="info"
                        size="small"
                        fullWidth
                        onClick={() => {
                          agentActivityRouteChange(value["id"]);
                        }}
                      >
                        view
                      </MDButton>
                    );
                    x["action"] = (
                      <>
                        <MDButton>
                          <DeleteIcon
                            variant="gradient"
                            color="error"
                            aria-label="delete"
                            sx={{
                              fontWeight: "bold",
                              cursor: "pointer",
                              transform: "scale(1.5)",
                            }}
                            onClick={() => {
                              handleClickOpen(value["id"]);
                            }}
                          >
                            Delete
                          </DeleteIcon>
                        </MDButton>
                        <Dialog
                          open={open}
                          onClose={handleClose}
                          aria-labelledby="alert-dialog-title"
                          aria-describedby="alert-dialog-description"
                        >
                          <DialogTitle id="alert-dialog-title">Information</DialogTitle>
                          <DialogContent>
                            <DialogContentText id="alert-dialog-description">
                              Are you sure you want to delete
                            </DialogContentText>
                          </DialogContent>
                          <DialogActions>
                            <MDButton onClick={handleClose}>No</MDButton>
                            <MDButton
                              onClick={() => {
                                deletedata(currentRowId);
                              }}
                            >
                              Yes
                            </MDButton>
                          </DialogActions>
                        </Dialog>
                      </>
                    );
                    return x;
                  }),
                }}
              />
            )}
          </MDBox>
        </Card>
      </MDBox>
    </>
  );
}

export default AgentsTable;
