import React from "react";

// @mui material components
import Card from "@mui/material/Card";
import { useLocation, useNavigate } from "react-router-dom";
import DashboardLayout from "examples/LayoutContainers/DashboardLayout";
import DashboardNavbar from "examples/Navbars/DashboardNavbar";
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";
import MDButton from "components/MDButton";
// Algo Craze components
import MDBox from "components/MDBox";
import { Toast } from "utils/constant";
import { useQuery } from "react-query";
import { UserBrokerStatusCheck } from "services/backend_api";
import IsLoad from "services/IsLoad";
import Brokerrecord from "./brokerrecord";
import ConnectAccount from "./ConnectAccount";

// const sessionId = localStorage.getItem("agent_session_id");
function UserBrokerIntegration() {
  const { state } = useLocation();
  const { uid } = state;
  const { data, isLoading } = useQuery("UserBrokerStatusCheck", () =>
    UserBrokerStatusCheck(uid)
      .then((res) => {
        if (res) {
          if (res.broker_status) {
            Toast.fire({
              icon: "error",
              title: `${res.broker_status}`,
            });
          }
          return res;
        }
        return false;
      })
      .catch((e) => {
        Toast.fire({
          icon: "error",
          title: `${e}`,
        });
      })
  );

  const navigate = useNavigate();

  return (
    <>
      <DashboardLayout>
        <DashboardNavbar />
        <MDBox pt={2}>
          <MDBox mt={-3} pt={2} px={2}>
            <MDButton
              variant="gradient"
              color="info"
              circular
              iconOnly
              style={{ borderRadius: 100 }}
              onClick={() => navigate(-1)}
            >
              <ArrowBackIosIcon fontSize="small" style={{ position: "absolute", right: 8.5 }} />
            </MDButton>
          </MDBox>
        </MDBox>
        <MDBox mt={3} mb={2}>
          {isLoading || data === undefined ? (
            <IsLoad />
          ) : (
            <Card mt={5}>
              {data.broker_integrated ? (
                <Brokerrecord broker_id={data.broker_id} broker_name={data.broker_name} uid={uid} />
              ) : (
                <ConnectAccount uid={state.uid} />
              )}
            </Card>
          )}
        </MDBox>
      </DashboardLayout>
    </>
  );
}
export default UserBrokerIntegration;
