import { API_URL, DB_NAME } from "utils/constant";
// import Cookies from "js-cookie";

export const session_string = "admin_session_id";
export const active_user_string = "active_user";

function buildRequest(params) {
  return JSON.stringify({
    jsonrpc: "2.0",
    method: "call",
    params: params, // payload
  });
}

export function isLoggedIn() {
  return localStorage.getItem(session_string) ? true : false;
}

export async function makeApiRequest(path, params, content_type) {
  try {
    try {
      let session_id = localStorage.getItem("admin_session_id");
      if (!session_id) {
        session_id = "";
      }
      const headers = {
        "Content-Type": content_type,
        "X-Openerp-Session-Id": session_id,
      };
      const response = await fetch(`${API_URL}${path}`, {
        method: "POST",
        headers: headers,
        // mode:'no-cors',
        body: buildRequest(params),
      });

      const rresponse = await response.json();

      if (rresponse.result && rresponse.result.session_id) {
        localStorage.setItem(session_string, rresponse.result.session_id);
      }

      if (!rresponse.error) {
        return { result: rresponse.result };
      } else {
        return { error: rresponse.error.data };
      }
    } catch (err) {
      console.error(err);
      return {
        error: {
          title: "Something went wrong",
          message: "Something went wrong",
        },
      };
    }
  } catch (error) {
    console.error(error);
    return {
      error: {
        title: "Api request error",
        message: "Api request error",
        error: error,
      },
    };
  }
}

// Admin LOGIN
export async function Authenticate(username, password, token) {
  const params = {
    db: DB_NAME,
    login: username,
    password: password,
    token: token,
  };

  const response = await makeApiRequest("/api/admin/login", params, "application/json");

  if (response.result) {
    // localStorage.setItem("remember", rememberMe);
    localStorage.setItem("admin", JSON.stringify(response.result));
    return response.result;
  } else if (response.error) {
    throw new Error(response.error.message);
  } else {
    throw new Error("Unknown error. Please try again.");
  }
}

