/* eslint-disable prettier/prettier */
/* eslint-disable react/destructuring-assignment */
/* eslint-disable react/prop-types */
/* eslint-disable arrow-body-style */
/* eslint-disable no-unused-expressions */

import React, { useState } from "react";
import MDBox from "components/MDBox";
import { Grid, Card } from "@mui/material";
import MDTypography from "components/MDTypography";
import DeleteIcon from "@mui/icons-material/Delete";
import MDButton from "components/MDButton";
import MDInput from "components/MDInput";

import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import { useMutation } from "react-query";
import { AdminUserBrokerDisconnect } from "services/backend_api";
import { Toast } from "utils/constant";

function Brokerrecord(props) {
  const [status, setStatus] = useState(false);
  const [open, setOpen] = useState(false);
  const handleClickOpen = () => {
    setOpen(true);
  };
  const mutation = useMutation("AdminUserBrokerDisconnect", (values) =>
    AdminUserBrokerDisconnect(values)
      .then((res) => {
        if (res) {
          if (res.error) {
            Toast.fire({
              icon: "error",
              title: `${res.error}`,
            });
          } else {
            Toast.fire({
              icon: "success",
              title: `Broker Deleted Successfully`,
            });
            setStatus(res);
          }
          return res;
        }
        return false;
      })
      .catch((e) => {
        Toast.fire({
          icon: "error",
          title: `${e}`,
        });
      })
  );
  if (status) {
    window.location.reload(true);
  }

  const handleYes = () => {
    mutation.mutate(props.uid);
    setOpen(false);
  };
  const handleNo = () => {
    setOpen(false);
  };

  return (
    <MDBox>
      <Card>
        <MDBox display="flex" justifyContent="space-between" style={{ alignItems: "center" }}>
          <MDTypography variant="h6" mt={3} ml={4} fontWeight="medium">
            Connected Demat Account
          </MDTypography>
          <MDBox mt={3} ml={4} mr={3}>
            <MDButton variant="gradient" color="error" onClick={handleClickOpen}>
              <DeleteIcon sx={{ fontWeight: "bold" }}>Delete</DeleteIcon>
              &nbsp;Delete
            </MDButton>
            <Dialog
              open={open}
              onClose={handleNo}
              aria-labelledby="alert-dialog-title"
              aria-describedby="alert-dialog-description"
            >
              <DialogTitle id="alert-dialog-title">Information</DialogTitle>
              <DialogContent>
                <DialogContentText id="alert-dialog-description">
                  Do you want to Disconnect Broker ?
                </DialogContentText>
              </DialogContent>
              <DialogActions>
                <MDButton onClick={handleNo}>No</MDButton>
                <MDButton onClick={handleYes} autoFocus>
                  Yes
                </MDButton>
              </DialogActions>
            </Dialog>
          </MDBox>
        </MDBox>

        <MDBox ml={7} mr={5} mt={4} mb={3}>
          <Grid container display="flex">
            <Grid item xs={12} sm={5} md={3} mb={2}>
              <MDInput disabled name="accountid" value={props.broker_id} />
            </Grid>
            <Grid item xs={12} sm={7} md={3} mb={2}>
              <MDInput disabled name="broker" value={props.broker_name} />
            </Grid>
          </Grid>
        </MDBox>
      </Card>
    </MDBox>
  );
}

export default Brokerrecord;
