/* eslint-disable no-console */
/* eslint-disable react/prop-types */
/* eslint-disable react/destructuring-assignment */
/* eslint-disable prettier/prettier */
/* eslint-disable import/no-unresolved */
/* eslint-disable new-cap */

import * as React from "react";
import MDTypography from "components/MDTypography";
import TextField from "@mui/material/TextField";
import AdapterDateFns from "@mui/lab/AdapterDateFns";
import LocalizationProvider from "@mui/lab/LocalizationProvider";
import TimePicker from "@mui/lab/TimePicker";
import moment from "moment";

export default function CustomTimePicker(props) {
  const [value, setvalue] = React.useState({
    start: moment(new Date()),
    end: moment(new Date()),
  });
  // const [start, setStart] = React.useState(null);
  // const [end, setEnd] = React.useState(null);

  const handleStartChange = (newValue) => {
    // setStart(newValue);
    setvalue({
      start: new moment(newValue),
      end: value.end,
    });
    props.setTime({
      start: new moment(newValue).format("HH:mm:ss"),
      end: value.end.format("HH:mm:ss"),
    });
  };
  const handleEndChange = (newValue) => {
    // setEnd(newValue);
    setvalue({
      start: value.start,
      end: new moment(newValue),
    });
    props.setTime({
      start: value.start.format("HH:mm:ss"),
      end: new moment(newValue).format("HH:mm:ss"),
    });
    // props.setDate(newValue);
  };

  return (
    <div style={{ display: "flex", alignItems: "center" }}>
      <LocalizationProvider dateAdapter={AdapterDateFns}>
        <TimePicker
          disabled={!props.id}
          value={value.start}
          onChange={handleStartChange}
          // onChange={(newValue) => {
          //   setStart(newValue);
          // }}
          renderInput={(params) => (
            <TextField {...params} className={!props.id ? "disField" : ""} />
          )}
        />
      </LocalizationProvider>
      <MDTypography variant="" fontWeight="regular" sx={{ margin: "0 10px" }}>
        to
      </MDTypography>
      <LocalizationProvider dateAdapter={AdapterDateFns}>
        <TimePicker
          disabled={!props.id}
          value={value.end}
          onChange={handleEndChange}
          renderInput={(params) => (
            <TextField {...params} className={!props.id ? "disField" : ""} />
          )}
        />
      </LocalizationProvider>
    </div>
  );
}
