/* eslint-disable dot-notation */
/* eslint-disable no-plusplus */
import React from "react";
import { CSVDownload } from "react-csv";
import { useLocation, useNavigate } from "react-router-dom";
import { REPORT } from "utils/constant";

const headers = [
  { label: "Number", key: "number" },
  { label: "Strategy", key: "strategy" },
  { label: "Script", key: "script" },
  { label: "Type", key: "type" },
  { label: "Date", key: "date" },
  { label: "Time", key: "time" },
  { label: "Action", key: "action" },
  { label: "PNL", key: "pnl" },
];

// const data = [{ fname: "Harsh", lname: "Ved" }];
function ReportCSV() {
  // console.log("CSV");
  const { state } = useLocation();
  const navigate = useNavigate();
  const { uid } = state;
  const path = REPORT;
  navigate(path, { state: { uid } });
  const data = state.sdata.map((value, ind) => {
    // eslint-disable-next-line prefer-const
    let x = {};
    // const d_id = value["id"]
    x["number"] = 1 + ind;
    x["strategy"] = value["strategy"];
    x["script"] = value["script"];
    x["type"] = value["type"];
    x["date"] = value["date"];
    x["time"] = value["time"];
    x["action"] = value["action"];
    x["pnl"] = value["pnl"];
    return x;
  });
  return <CSVDownload data={data} headers={headers} target="_blank" filename="report.csv" />;
}

export default ReportCSV;
