/* eslint-disable no-undef */
/* eslint-disable no-console */
/* eslint-disable react/prop-types */
/* eslint-disable react/destructuring-assignment */
/* eslint-disable prettier/prettier */
/* eslint-disable import/no-unresolved */
/* eslint-disable new-cap */
import MDTypography from "components/MDTypography";
import LocalizationProvider from "@mui/lab/LocalizationProvider";
import AdapterDateFns from "@mui/lab/AdapterDateFns";
import DatePicker from "@mui/lab/DatePicker";
import * as React from "react";
import TextField from "@mui/material/TextField";
import moment from "moment";
import { useState } from "react";
import './reportStyle.css';

export default function CustomDatePicker(props) {
  const [value, setvalue] = useState({
    start: moment(new Date()).format(),
    end: moment(new Date()).format(),
  });
  // const [start, setStart] = React.useState(new Date());
  // const [end, setEnd] = React.useState(new Date());

  const handleStartChange = (newValue) => {
    // setStart(newValue);
    setvalue({
      start: new moment(newValue).format("YYYY/MM/DD"),
      end: value.end,
    })
    props.setDate({
      start: new moment(newValue).format("YYYY/MM/DD"),
      end: value.end,
    });
  }
  const handleEndChange = (newValue) => {
    // setEnd(newValue);
    setvalue({
      start: value.start,
      end: new moment(newValue).format("YYYY/MM/DD"),
    });
    props.setDate({
      start: value.start,
      end: new moment(newValue).format("YYYY/MM/DD"),
    });
    // props.setDate(newValue);
  }
  return (
    <div m={1} style={{display: 'flex', alignItems: 'center'}}>
    <LocalizationProvider dateAdapter={AdapterDateFns}>
        <DatePicker
          type="date"
          disabled={!props.id}
          disableFuture
          openTo="day"
          views={['year', 'month', 'day']}
          value={value.start}
          onChange={handleStartChange}
          renderInput={(params) => <TextField {...params} className = {!props.id ? "disField" : ""}/>}
        />
        <MDTypography variant="" fontWeight="regular" sx={{margin: "0 10px"}}>
          to
        </MDTypography>
        <DatePicker
          disabled={!props.id}
          sx={{ height: 40 }}
          disableFuture
          openTo="day"
          views={['year', 'month', 'day']}
          value={value.end}
          onChange={handleEndChange}
          renderInput={(params) => <TextField {...params} className = {!props.id ? "disField" : ""}/>}
        />
    </LocalizationProvider>
    </div> 

    
  );
}
