/* eslint-disable dot-notation */
import React, { useState } from "react";
import DataTable from "examples/Tables/DataTable";
import IsLoad from "services/IsLoad";
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";
import { useQuery, useMutation } from "react-query";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import ArrowDropDownIcon from "@mui/icons-material/ArrowDropDown";
import MenuItem from "@mui/material/MenuItem";
import Grid from "@mui/material/Grid";
import { UserSubscriptionList, GetAgent, UpdateUserAgent } from "services/backend_api";
import MDBox from "components/MDBox";
import MDTypography from "components/MDTypography";
import MDButton from "components/MDButton";
import { Card, Select } from "@mui/material";
import { useLocation, useNavigate } from "react-router-dom";
import DashboardLayout from "examples/LayoutContainers/DashboardLayout";
import DashboardNavbar from "examples/Navbars/DashboardNavbar";
import { Toast, MANAGEAGENTS } from "utils/constant";

function UserDetailTable() {
  const [agent, setAgent] = useState("");
  const navigate = useNavigate();
  const { state } = useLocation();
  const { data, isLoading } = useQuery("UserSubscriptionList", () =>
    UserSubscriptionList(state.uid)
      .then((res) => {
        if (res) {
          if (res.error) {
            Toast.fire({
              icon: "error",
              title: `${res.error}`,
            });
          }
          return res;
        }
        return false;
      })
      .catch((e) => {
        Toast.fire({
          icon: "error",
          title: `${e}`,
        });
      })
  );
  const { data: agentList, isLoadingAgent } = useQuery("GetAgent", () =>
    GetAgent()
      .then((res) => {
        if (res) {
          if (res.error) {
            Toast.fire({
              icon: "error",
              title: `${res.error}`,
            });
          }
          return res;
        }
        return false;
      })
      .catch((e) => {
        Toast.fire({
          icon: "error",
          title: `${e}`,
        });
      })
  );
  const mutation = useMutation("UpdateUserAgent", () =>
    UpdateUserAgent(agent, state.uid)
      .then((res) => {
        // console.log(res);
        if (res) {
          if (res.error) {
            Toast.fire({
              icon: "error",
              title: `${res.error}`,
            });
          } else {
            Toast.fire({
              icon: "success",
              title: `Agent Updated Successfully`,
            });
          }
          navigate(MANAGEAGENTS);
          return res;
        }
        return false;
      })
      .catch((e) => {
        Toast.fire({
          icon: "error",
          title: `${e}`,
        });
      })
  );
  const handleOnChange = (event) => {
    setAgent(event.target.value);
  };
  const handleSubmit = () => {
    mutation.mutate(agent);
  };

  return (
    <>
      <DashboardLayout>
        <DashboardNavbar />
        <MDBox pt={2}>
          <MDBox mt={-3} pt={2} pb={2} px={2}>
            <MDButton
              variant="gradient"
              color="info"
              circular
              iconOnly
              style={{ borderRadius: 100 }}
              onClick={() => navigate(-1)}
            >
              <ArrowBackIosIcon fontSize="small" style={{ position: "absolute", right: 8.5 }} />
            </MDButton>
          </MDBox>
        </MDBox>
        <MDBox mt={3}>
          <MDBox mb={7}>
            <Card>
              <MDBox p={4} alignItems="center">
                <Grid
                  container
                  spacing={2}
                  display="flex"
                  justifyContent="space-between"
                  alignItems="center"
                >
                  <Grid item xs={12} sm={4} md={4} mb={2}>
                    <MDTypography variant="h5" fontWeight="regular">
                      Name : {state.name}
                    </MDTypography>
                  </Grid>
                  <Grid item xs={12} sm={5} md={5} mb={2}>
                    <MDTypography variant="h5" fontWeight="regular">
                      Email : {state.email}
                    </MDTypography>
                  </Grid>
                  <Grid item xs={12} sm={3} md={3} mb={2}>
                    <MDTypography variant="h5" fontWeight="regular">
                      Phone No. : {state.mobile}
                    </MDTypography>
                  </Grid>
                </Grid>
                <Grid
                  container
                  spacing={2}
                  display="flex"
                  justifyContent="space-between"
                  alignItems="center"
                >
                  <Grid item xs={12} sm={4} md={4} mb={2}>
                    <MDTypography variant="h5" fontWeight="regular">
                      Agent Name : {state.agentName}
                    </MDTypography>
                  </Grid>
                  <Grid item xs={12} sm={5} md={5} mb={2}>
                    <MDTypography variant="h5" fontWeight="regular">
                      Total PnL : {state.pnl}
                    </MDTypography>
                  </Grid>
                  <Grid item xs={12} sm={3} md={3} mb={2}>
                    <MDTypography variant="h5" fontWeight="regular">
                      Total Subscription : {state.totalSub}
                    </MDTypography>
                  </Grid>
                </Grid>
                {isLoading || isLoadingAgent || agentList === undefined ? (
                  <IsLoad />
                ) : (
                  <Grid container spacing={2} display="flex" alignItems="center">
                    <Grid item xs={12} sm={4} md={4} mb={2}>
                      <FormControl style={{ width: "100%" }}>
                        <InputLabel id="demo-simple-select-label">Update Agent</InputLabel>
                        <Select
                          sx={{ height: 45 }}
                          IconComponent={() => <ArrowDropDownIcon />}
                          labelId="demo-simple-select-label"
                          label=" Update Agent"
                          id="agentName"
                          name="agentName"
                          onChange={handleOnChange}
                        >
                          {agentList.map((value) => (
                            <MenuItem value={value.id}>{value.name}</MenuItem>
                          ))}
                        </Select>
                      </FormControl>
                    </Grid>
                    <Grid item xs={12} sm={4} md={4} mb={2}>
                      <MDButton
                        variant="gradient"
                        type="submit"
                        color="dark"
                        onClick={() => {
                          handleSubmit();
                        }}
                      >
                        Update
                      </MDButton>
                    </Grid>
                  </Grid>
                )}
              </MDBox>
            </Card>
          </MDBox>
          <Card style={{ padding: "15px" }}>
            <MDBox
              mx={2}
              mt={-6}
              py={3}
              px={2}
              variant="gradient"
              bgColor="info"
              borderRadius="lg"
              coloredShadow="info"
            >
              <MDTypography variant="h6" color="white">
                Subscription List
              </MDTypography>
            </MDBox>
            <MDBox>
              {isLoading || data === undefined ? (
                <IsLoad />
              ) : (
                <DataTable
                  table={{
                    columns: [
                      { Header: "id", accessor: "id", align: "center" },
                      { Header: "Algo Name", accessor: "algo_name", align: "center" },
                      { Header: "Script", accessor: "script", align: "center" },
                      {
                        Header: "Subcription date & time",
                        accessor: "subDateTime",
                        align: "center",
                      },
                      { Header: "Lot Size", accessor: "lot_size", align: "center" },
                    ],
                    rows: data.map((value, ind) => {
                      // eslint-disable-next-line prefer-const
                      let x = {};
                      x["id"] = 1 + ind;
                      x["algo_name"] = value["algoname"];
                      x["script"] = value["script"];
                      x["subDateTime"] = value["date"];
                      x["lot_size"] = value["lot_size"];
                      return x;
                    }),
                  }}
                />
              )}
            </MDBox>
          </Card>
        </MDBox>
      </DashboardLayout>
    </>
  );
}

export default UserDetailTable;
