/* eslint-disable no-undef */
/* eslint-disable no-param-reassign */
/* eslint-disable react/prop-types */
/* eslint-disable no-console */
/* eslint-disable react/destructuring-assignment */
/* eslint-disable no-unused-vars */
/* eslint-disable prettier/prettier */
import * as React from 'react';
import OutlinedInput from '@mui/material/OutlinedInput';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import ListItemText from '@mui/material/ListItemText';
import Select from '@mui/material/Select';
import Checkbox from '@mui/material/Checkbox';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
// import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

let names = [];
const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 4;
const MenuProps = {
  PaperProps: {
    style: {
      fontSize: 4,
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: "auto",
    },
  },
};

const Day = [
  'Monday',
  'Tuesday',
  'Wednesday',
  'Thursday',
  'Friday',
];
const Strategy = [
  'Star Bull',
  'Sky Bull',
  'Bull',
];
const Script = [
  'TCS',
  'Infosys',
  'Rategain',
];

export default function MultipleSelectCheckmarks(props) {
  const [personName, setPersonName] = React.useState([]);
  if(props.id === "undefined") {
    names = []; 
    // console.log("INside")
    setPersonName([])
  }
  // if(props.setDay === null){setPersonName(null)}
  if(props.name === 'Day'){
    names = Day;
  }else if(props.name === 'Strategy'){
    names = Strategy;
  }else if(props.name === 'Script'){
    names = Script;
  }
  let isDisabled =  false;
  if (props.isVisible === 'true'){
    isDisabled = true
  }
  const handleChange = (event) => {
    const {
      target: { value },
    } = event;
    setPersonName(
      // On autofill we get a stringified value.
      typeof value === 'string' ? value.split(',') : value,
    );
    if(props.name === 'Day'){
      props.setDay({value})
    }else if(props.name === 'Strategy'){
      props.setStrategy({value})
    }else if(props.name === 'Script'){
      props.setScript({value})
    }
  };
  return ( 
    <div>
      <FormControl sx={{ m: 0, width: "200px"}}>
        <InputLabel id="demo-multiple-checkbox-label">{props.name}</InputLabel>
        <Select
          disabled={!props.id}
          sx={{height: 40}}
          IconComponent={() => <ArrowDropDownIcon />}
          labelId="demo-multiple-checkbox-label"
          id="demo-multiple-checkbox"
          multiple
          value={personName}
          onChange={handleChange}
          label={props.name}
          // input={<OutlinedInput label={props.name} />}
          className = {!props.id ? "disField" : ""}
          renderValue={(selected) => selected.join(', ')}
          MenuProps={MenuProps}
        >
          {names.map((name) => (
            <MenuItem key={name} value={name} >
              <Checkbox checked={personName.indexOf(name) > -1} />
              <ListItemText primary={name} />
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    </div>
  );
}