export const Toast = swal.mixin({
  toast: true,
  position: "top-end",
  showConfirmButton: false,
  timer: 3000,
  timerProgressBar: true,
  didOpen: (toast) => {
    toast.addEventListener("mouseenter", swal.stopTimer);
    toast.addEventListener("mouseleave", swal.resumeTimer);
  },
});

export function CustomDialog() {
  const [open, setOpen] = useState(false);

  const handleClose = () => {
    setOpen(false);
  };
  return (
    <>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">Information</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">{/* {props.title} */}</DialogContentText>
        </DialogContent>
        <DialogActions>
          <MDButton onClick={handleClose}>No</MDButton>
          <MDButton>Yes</MDButton>
        </DialogActions>
      </Dialog>
    </>
  );
}

export function CustomSelect({ onChange, options, value }) {
  // eslint-disable-next-line no-shadow
  const defaultValue = (options, value) =>
    options ? options.find((option) => option.value === value) : "";

  return (
    <div className="css-12n1zae-MuiInputBase-root-MuiOutlinedInput-root">
      <Select
        styles={{ width: "100%", zIndex: 6555555 }}
        value={defaultValue(options, value)}
        onChange={(values) => {
          onChange(values);
        }}
        options={options}
      />
    </div>
  );
}
