/* eslint-disable dot-notation */
import React from "react";
import DataTable from "examples/Tables/DataTable";
import IsLoad from "services/IsLoad";
// import DeleteIcon from "@mui/icons-material/Delete";
import { useQuery } from "react-query";
import { GetSignal } from "services/backend_api";
import MDBox from "components/MDBox";
import MDTypography from "components/MDTypography";
import { Card } from "@mui/material";
import { Toast } from "utils/constant";

function SignalTable() {
  const { data, refetch, isLoading } = useQuery("GetSignal", () =>
    GetSignal()
      .then((res) => {
        if (res) {
          if (res.error) {
            Toast.fire({
              icon: "error",
              title: `${res.error}`,
            });
          }
          return res;
        }
        refetch();
        return false;
      })
      .catch((e) => {
        Toast.fire({
          icon: "error",
          title: `${e}`,
        });
      })
  );
  return (
    <>
      <MDBox mt={5}>
        <Card style={{ padding: "15px" }}>
          <MDBox
            mx={2}
            mt={-6}
            py={3}
            px={2}
            variant="gradient"
            bgColor="info"
            borderRadius="lg"
            coloredShadow="info"
          >
            <MDTypography variant="h6" color="white">
              Signal List
            </MDTypography>
          </MDBox>
          <MDBox>
            {isLoading || data === undefined ? (
              <IsLoad />
            ) : (
              <DataTable
                table={{
                  columns: [
                    { Header: "id", accessor: "id", align: "center" },
                    { Header: "Strategy", accessor: "strategy", align: "center" },
                    { Header: "Price", accessor: "price", align: "center" },
                    { Header: "Action", accessor: "action", align: "center" },
                    { Header: "Date", accessor: "date", align: "center" },
                    { Header: "PNL", accessor: "pnl", align: "center" },
                    { Header: "Type", accessor: "type", align: "center" },
                    { Header: "Day", accessor: "day", align: "center" },
                    { Header: "Qty", accessor: "quantity", align: "center" },
                    { Header: "Symbol", accessor: "symbol", align: "center" },
                    { Header: "sq off", accessor: "is_squreoff", align: "center" },
                  ],
                  rows: data.map((value, ind) => {
                    // eslint-disable-next-line prefer-const
                    let x = {};
                    x["id"] = 1 + ind;
                    x["strategy"] = value["strategy"];
                    x["price"] = value["price"];
                    x["action"] = value["action"];
                    x["date"] = value["date"];
                    x["pnl"] = value["pnl"];
                    x["type"] = value["type"];
                    x["day"] = value["day"];
                    x["quantity"] = value["quantity"];
                    x["symbol"] = value["symbol"];
                    // x["is_squreoff"] = value["is_squreoff"];
                    if (value["is_squreoff"] === false) {
                      x["is_squreoff"] = "False";
                    } else {
                      x["is_squreoff"] = "True";
                    }
                    return x;
                  }),
                }}
              />
            )}
          </MDBox>
        </Card>
      </MDBox>
    </>
  );
}

export default SignalTable;
