/* eslint-disable import/no-cycle */
/* eslint-disable react/destructuring-assignment */
/* eslint-disable react/prop-types */
import React, { useState } from "react";

import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";

import ArrowDropDownIcon from "@mui/icons-material/ArrowDropDown";
import MDTypography from "components/MDTypography";
import Select from "@mui/material/Select";
import Grid from "@mui/material/Grid";
import MDBox from "components/MDBox";

import Angleone from "./angleone";
import Aliceblue from "./aliceblue";

const ConnectAccount = (props) => {
  const [brokername, setBrokername] = useState("angelone");
  const handleChange = (event) => {
    setBrokername(event.target.value);
  };
  return (
    <>
      <MDBox mt={5} mb={5}>
        <MDTypography variant="h6" mt={3} mb={2} ml={4} fontWeight="medium">
          Connect Your Account
        </MDTypography>
        <Grid container spacing={2} mr={4} ml={5} mt={2}>
          <FormControl md={3} sm={12} lg={4}>
            <InputLabel id="demo-simple-select-label">Link Account</InputLabel>
            <Select
              sx={{ height: 45, width: 200 }}
              IconComponent={() => <ArrowDropDownIcon />}
              labelId="demo-simple-select-label"
              label="Link Account"
              id="demo-simple-select"
              onChange={handleChange}
              defaultValue={brokername}
              inputProps={{
                name: "angelone",
                id: "uncontrolled-native",
              }}
            >
              <MenuItem value="angelone">Angel One</MenuItem>
              <MenuItem value="aliceblue">Alice Blue</MenuItem>
            </Select>
          </FormControl>
        </Grid>
        <MDBox>
          <MDBox mb={2} mt={2} ml={5} mr={5} m={5}>
            {brokername === "angelone" ? (
              <Angleone uid={props.uid} />
            ) : (
              <Aliceblue uid={props.uid} />
            )}
          </MDBox>
        </MDBox>
      </MDBox>
    </>
  );
};

export default ConnectAccount;
